@import '~src/css/quasar.variables.sass', 'quasar/src/css/variables.sass'

.payments-page
  .pricing-section
    display: flex
    justify-content: center
    align-items: center
  .benefits-list
    list-style: none
    margin: 0 0 0 20px
    padding: 0
    i
      font-size: 20px
    li
      margin: 2px 0
  .pricing-section
    margin: 10px 0
  .pricing-card-container
    display: flex
    flex-wrap: wrap
    justify-content: center
    align-items: flex-start
  .pricing-card
    margin: 10px
    width: 95%
  .pricing-card-title
    padding: 0
  .pricing-card-main
    line-height: 1.5
    padding: 10px
  .pricing-card-insurance-logo
    width: 40px
    vertical-align: middle
  .pricing-card-pricing-point
    font-size: 125%
  .pricing-plan-description
    padding: 5px 0
    border-bottom: 1px solid $grey-4
    >span
      float: right
      margin-right: 50px
