
.tutoring-job-details
  .applicant-groups
    .q-tab-label
      font-size: 0.75rem
    .q-chip
      font-size: 75%
      min-height: 10px
      right: 10px
      font-weight: bold
    .q-tab:not(:last-child)
      border-right: 1px solid #fff
  .applicants-tab-pane
    padding: 0
  .job-actions
    position: fixed
    bottom: 0
    left: 0
    right: 0
    display: flex
    .q-btn
      flex: 1
      border-radius: 0
    .cancel-job
      flex-grow: 1.1
    .invite-more
      flex-grow: 1.4
