@import '~src/css/quasar.variables.sass', 'quasar/src/css/variables.sass'
.q-loading
  transition: none !important

.q-inner-loading
  transition: none !important
  background-color: white

.q-loading .q-loading__spinner
  display: none

.q-loading__backdrop
  opacity: 1

.q-field-prepend-override, .q-field--float
  .q-field__prepend
    align-items: start
    margin-top: 5px

.q-badge--floating
  top: -6px

.section-groups, .applicant-groups
  .q-tabs
    background-color: $primary-2
  .q-tab--inactive
    opacity: 1
  .q-tab
    text-transform: none
    background-color: $primary-2
    padding: 8px 0
  .q-tab__indicator
    background-color: white
    height: 5px
  .q-tab__label
    font-size: 0.75rem
    letter-spacing: 1px
  .q-tab__alert
    width: 10px
    height: 10px
  .q-chip
    border-radius: 10px
    min-height: 10px
    right: 10px
    font-size: 75%
