@import '~src/css/quasar.variables.sass', 'quasar/src/css/variables.sass'

.recurring-job-details
  .applicant-card
    border-bottom: 1px solid $grey-1
    margin-bottom: 1rem
    .applicant-info
      display: flex
      align-items: flex-start
      padding: 0.5rem
      &-image
        margin-right: 0.5rem
      &-other
        font-size: 0.9rem

  .availability
    background: $grey-1
    .availability-item
      display: flex
      justify-content: space-between
      padding: 1rem 0
      border-bottom: 1px solid $grey-4
      :last-child
        border: none
      &-timings
        display: flex
        flex-direction: column
        .availability-icon
          margin-left: 0.5rem
    .actions-container
      padding-top: 1rem
      display: flex
      justify-content: space-around
