@import '~src/css/quasar.variables.sass', 'quasar/src/css/variables.sass'

.one-off-job-details
  .applicant-card
    border-bottom: 1px solid $grey-1
    margin-bottom: 5px
    .applicant-info
      display: flex
      align-items: flex-start
      padding: 0.5rem
      &-image
        margin-right: 0.5rem
      &-other
        font-size: 0.9rem
  .actions-container
    margin-left: 20px
    padding-top: 0
    display: flex
    justify-content: space-around
