
.helper-profile-card
  height: 285px
  position: relative
  margin: 2px 0
  width: 50%
  border: 1px solid white
  .img-container
    overflow: hidden
    img
      height: 150px
      max-width: 150px
  .q-card-section
    padding: 8px
  .disabled
    opacity: 0.5
  .video-container
    position: absolute
    top: 2px
    left: 2px
    width: 150px
    height: 150px
    color: white
    display: block
    text-align: left
    .overlay-video-icon
      background-color: transparent
      font-size: 30px
      width: 35x
      height: 35px
      display: inline-block
      margin-top: 5px
      margin-left: 5px
