@import '~src/css/quasar.variables.sass', 'quasar/src/css/variables.sass'

.pending-profile-banner
  margin-top: 5px
  padding: 0.5rem
  .completeness-msg
    padding-top: 0
  .completeness-bar
    width: 100%
    height: 1.5rem
    background: $blue-grey-1
    border-radius: 20px
    border: 1px solid $blue-grey-4
    .completed
      height: 100%
      border-radius: 20px
      background: $brand-blue
