@import '~src/css/quasar.variables.sass', 'quasar/src/css/variables.sass'

.profile-ranking
  .ranking-bar
    .completeness-msg
      padding: 0.5rem 0
    .completeness-bar
      width: 100%
      height: 1.5rem
      background: $blue-grey-1
      border: 1px solid $blue-grey-4
      .completed
        height: 100%
        background: $blue-grey-6
      .pending
        height: 100%
