@import '~src/css/quasar.variables.sass', 'quasar/src/css/variables.sass'
// app global css in Sass form
body
	overscroll-behavior: none

.button-link
	text-decoration: underline

.round-user-image
	width: 3.5em
	height: 3.5em
	border: 1px solid rgba(0,0,0,0.2)
	-webkit-border-radius: 4em
	border-radius: 4em
	vertical-align: middle

.notice
	border-left: 5px solid $brand-orange

.text-brand-pink
	color: $brand-pink !important

.text-brand-orange
	color: $brand-orange !important

.text-brand-light
	color: $brand-light !important

.text-brand-gold
	color: $brand-gold !important

.text-light-red
	background-color: $brand-light-red !important

.text-underline
	text-decoration-line: underline

.text-secondary-lighter
	color: rgba(28, 28, 28, 0.54)

.bg-secondary-lighter
	background-color: rgba(28, 28, 28, 0.54)

.bg-brand-grey
	background-color: $brand-grey !important

.bg-light-red
	background-color: $brand-light-red !important

.item-alert
	position: absolute
	border-radius: 50%
	top: 7px
	right: 7px
	height: 7px
	width: 7px
	background: red

.ellipsis
	overflow: hidden
	text-overflow: ellipsis
	white-space: nowrap

.text-wrap
	white-space: normal

.card-container, .invited-carers
	@media (min-width: 750px)
		.helper-profile-card, .client-profile-card, .helper-profile-card-skeleton, .client-profile-card-skeleton
			width: 33%
	@media (min-width: 1000px)
		.helper-profile-card, .client-profile-card, .helper-profile-card-skeleton, .client-profile-card-skeleton
			width: 25%
	@media (min-width: 1500px)
		.helper-profile-card, .client-profile-card, .helper-profile-card-skeleton, .client-profile-card-skeleton
			max-width: 250px

.invited-carers
	margin-bottom: 3rem

// @media (max-width: 767px)
.layout-padding
	padding: 0.5rem 0.5rem

.upsell .upsell-insurance-logo
	width: 3rem
	vertical-align: middle

.upsell .upsell-payment-separator
	text-align: center
	padding: 5px
	height: auto
	min-height: auto

.upsell .upsell-payment-or
	font-size: 14px
	font-weight: bold
	text-transform: uppercase

.upsell .upsell-payment-header
	min-height: 0
	color: #000
	padding: 0

.upsell .upsell-check-icon
	font-size: 16px
	margin-right: 5px
	margin-bottom: 2px

.upsell .upsell-subscription-benefits
	padding: 0 0 0 20px
	text-align: left
	margin-left: 40px

.upsell .upsell-item
	padding: 5px
	border-bottom: 1px solid $grey-4

.absolute-position
	position: absolute
	top: 0
	right: 0

.price-guide-table, .pricing-table, .job-summary-table
	width: 100%
	max-width: 100%
	border-collapse: separate
	border-spacing: 0
	td
		padding: 5px 0
	th
		font-weight: bold

.job-summary-table
	td, th
		text-align: center

.header-logo
	height: 25px
	vertical-align: middle

.js-logo
	src: url("~assets/js-logo.png")

.box-primary
	font-size: 12px
	font-weight: bold
	background: $primary
	color: white
	display: inline-block

.box-primary-rounded
	font-weight: bold
	background: $primary
	color: white
	border-radius: 4px
	display: inline-block
