@import '~src/css/quasar.variables.sass', 'quasar/src/css/variables.sass'

.onboarding-header
  display: flex
  align-items: center
  justify-content: center
.onboarding-header-icon
  border-radius: 30px
  padding: 5px
  border: 2px solid $primary
.onboarding-image
  pointer-events: none
  height: calc(100vh - 180px)
.onboarding-intro
  background: url('/images/onboarding-intro.jpg') no-repeat
  background-size: cover
  width: 100%
  height: calc(100vh - 58px)
.onboarding-intro-img
  object-fit: cover
  width: 100%
  height: 100vh
.onboarding-action
  position: fixed
  left: 0
  right: 0
